<script>
import CorePlans from "../plans/CorePlans.svelte";

import BasicForm from "./steps/BasicForm.svelte";
import PaymentAnyElement from "./steps/PaymentAnyElement.svelte";
import PaymentFunnels from "./steps/PaymentFunnels.svelte";
import SignInForm from "./steps/SignInForm.svelte";
import { StoreForm } from "./steps/StoreForm";
let Data = tripod_register;
function getRegisterData(){
    return tripod_register;
}

let register_data = getRegisterData();

/**
 * Steps
*/
let step = "BasicForm";
let registerData = {};

function changeStep(data){
    step = data;
    return step;
}


let error = false;
let warning = false;

const errors = {
    showError: (message)=>{
        error =  message;
    },
    showWarning: (message)=>{
        warning =  message;
    },
    hide: ()=>{
        error = false;
        warning = false;
    }
}

let showModuleRegister = true;

let selectedPlan = {};
function onSelectPlan({plan, interval, button}){
    StoreForm.update("plansFetched", [plan]);
    selectedPlan = {
        plan: plan,
        interval: interval,
        button: button,
    }
    changeStep("PaymentAnyElement");
}
let box = null;
$: {
    if(step){
        if(step === "ChoosePlan"){
            showModuleRegister = false;
            if(box !== null){
                box.style.width = "100%";
                box.style.maxWidth = "1360px";
                box.style.minWidth = "320px";
            }

        }else if(step === "PaymentAnyElement"){
            showModuleRegister = true;
            if(box !== null){
                box.style.width = "100%";
                box.style.maxWidth = "1360px";
                box.style.minWidth = "320px";
            }
        }else{
            showModuleRegister = true;
            if(box !== null){
                box.style.width = "100%";
                box.style.maxWidth = "640px";
                box.style.minWidth = "320px";
            }
        }
    }
}

</script>
<style>
.container-form{
    padding: 3.75rem 2rem;
}
.CoreRegister :global(*:not(i):not(.icon):not[class*='plan-icons']){
    font-family: 'Montserrat', sans-serif !important;
}
.CoreRegister :global(.under--navigation-back){
    margin-bottom: 0.25rem;
}
</style>
{#if showModuleRegister === true}
    <section class="columns is-multiline is-centered CoreRegister">
        <div class="column is-12">
            <div class="box has-text-centered container-form" bind:this="{box}">
                {#if step === "SignInForm"}
                    <SignInForm Data={Data} errors={errors} changeStep={changeStep} />
                {/if}
                {#if step === "BasicForm"}
                    <BasicForm Data={Data} errors={errors} changeStep={changeStep} />
                {/if}            
                {#if step === "PaymentFunnels"}
                    <PaymentFunnels Data={Data} errors={errors} changeStep={changeStep} />
                {/if}
                {#if step === "PaymentAnyElement"}
                    <PaymentAnyElement selectedPlan={selectedPlan} Data={Data} errors={errors} changeStep={changeStep} />
                {/if}
                {#if error !== false && typeof error === "string"}
                    <div class="errors">
                        <p class="has-text-danger has-text-centered">
                            {error}
                        </p>
                    </div>
                {/if}
                {#if warning !== false && typeof warning === "string"}
                    <div class="warnings">
                        <p class="has-text-warning fw600 has-text-centered">
                            {warning}
                        </p>
                    </div>
                {/if}
            </div>
        </div>
    </section>
{/if}
{#if step === "ChoosePlan"}
    <!-- on module_plans active. -->
    <CorePlans Data={Data} onSelectPlan={onSelectPlan} errors={errors} changeStep={changeStep} />
{/if}
