<script>
import { turpial } from '../../libraries/turpial.js';
import StripeCard from './../../components/Elements/StripeCard.svelte';
// import InternationalNumberInput from './../../components/Elements/InternationalNumberInput.svelte';
import { random_string } from '../../libraries/random_string.js';

import { StoreForm } from './StoreForm.js';
import { AppForms } from '../../libraries/AppForms.js';
export let changeStep;
export let Data;
export let errors;
let stripe_key;
Data.is_online = Data.is_online ?? true;
if(Data.is_online === true){
    stripe_key = Data.stripeTokens.live;
}else{
    stripe_key = Data.stripeTokens.test;
}
let lang = Data.lang;
let Affiliate = Data.affiliate;
let trial = Data.trial ?? false;
let formLang = lang.forms.planSelection;
let selectedPrice;
let buttonSubmit;
let form = null;
let IS_ONLINE =  tripod_register.is_online ?? false;
let couponElement = null;
let terms = false;
let hasDiscountFromTag  = false;
export let selectedPlan = {};
let plan = selectedPlan.plan;

$: {
    if(terms !== false){
        terms.parentNode.parentNode.parentNode.style.minHeight = `${terms.offsetHeight + 16}px`;
    }
}

function sendEvent(){
    try {
        let dataRegister = $StoreForm.dataForm;
        dataRegister.userFromCampaign = tripod_register.userFromCampaign;
        dataRegister.type_event = "Widget Registration";
        
        let url;
        if(IS_ONLINE === false){
            url = "http://localhost/app.3pod.io/public/api/v1/events/registerUser";
        }else{
            url = "https://app.3pod.io/public/api/v1/events/registerUser";
        }
        console.log(dataRegister)
        turpial.fetch({
            url: [url],
            method: "POST",
            type: "text",
            data: JSON.stringify({
                token: btoa(JSON.stringify(dataRegister)).split("").reverse().join(""),
            }),
            ready: (data)=>{
                console.log(data)
            }
        })
    } catch (error) {
        console.log(error)
    }
}
sendEvent();


function updateDataForm(){
    // update any error
    errors.hide();

    let data = AppForms.retrieve({
        form: form,
        targets: ".formElements",
    })
    StoreForm.update("dataForm", data, $StoreForm.dataForm);
    data = $StoreForm.dataForm;
    return data;
}

let hasErrorForms = false;
function checkForm(data){
    let is_all_valid = true;
    data = data || {};

    // checks validation inputs.
    if(couponElement !== null){
        if(couponElement.dataset.valid === "false"){ is_all_valid = false; }
    }
    


    // check empty inputs
    let checks = ["first-name", "last-name", "email", "password", "phone", "city", "card_name_owner"];
    checks.map(( element )=>{
        if(typeof data[element] !== "string" && data[element] === ""){
            is_all_valid = false;
        }
    });

    
    
    return is_all_valid;
}

let stripeToken = "";
function submitPayment(){
    let data = updateDataForm();
    
    if(checkForm(data) === false){ 
        AppForms.statusButton(buttonSubmit, "enabled");
        errors.showError(lang.errors.uncompleted);
        return;
    }
    AppForms.statusButton(buttonSubmit, "disabled");
    tripod_register.stripe.module.createToken(tripod_register.stripe.card).then(function(result) {
        if (result.error) {
            // Inform the user if there was an error.
            tripod_register.stripe.cardErrorElement.textContent = result.error.message;
            AppForms.statusButton(buttonSubmit, "enabled");
        } else {
            // Send the token to your server.
            // send data to server             
            data.stripeToken = result?.token?.id||"none";
            data.affiliate = Affiliate;
            data.userFromCampaign = tripod_register.userFromCampaign||"widget-registration";
            data.interval = plan.interval_selected;
            data.id_plan = plan.__id__;
            data.membership_type = plan.membership_type;

            
            let url;
            let path = "public/api/v1/create_customer_subscription";
            if(IS_ONLINE === false){
                url = `${Data?.host_offline ?? "http://localhost/app.3pod.io/"}${path}`;
            }else{
                url = `https://app.3pod.io/${path}`;
            }

            turpial.fetch({
                url: [url],
                data: JSON.stringify(data),
                method: "POST",
                type: "text",
                ready: (data)=>{                    
                    data = JSON.parse(data[0]);
                    let message = data?.message ?? "";
                    if(data?.status === "error"){
                        errors.showError(message);
                        AppForms.statusButton(buttonSubmit, "enabled");
                        return;
                    }

                    let redirect = data?.response?.url ?? null; 
                    if(redirect === null){
                        errors.showError(message);
                        return;
                    }
                    setTimeout(() => {
                        window.location.href = redirect;
                    }, 450);
                },
                onerror: (data)=>{
                    console.error(data)
                    AppForms.statusButton(buttonSubmit, "enabled");
                }
            })

        }
    });
}
function checkCoupon(event){
    let input = event.target;
    if(input.value === ""){
        input.classList.remove("coupon-success");
        input.classList.remove("coupon-error");
        input.dataset.valid = true;
        return;
    }

    if(input.value.length <= 3){
        input.classList.add("coupon-error");
        input.classList.remove("coupon-success");
        input.dataset.valid = false;
        parsePlans();
        return;
    }

    let url;
    if(IS_ONLINE === false){
        url = "http://localhost/app.3pod.io/public/api/v1/getCoupon?coupon="+input.value;
    }else{
        url = "https://app.3pod.io/public/api/v1/getCoupon?coupon="+input.value;
    }


    turpial.fetch({
        url: [url],
        method: "POST",
        type: "text",
        ready: (data)=>{
            data = JSON.parse(data[0]);
            if(data?.status === "success"){
                input.classList.add("coupon-success");
                input.classList.remove("coupon-error");
                input.dataset.valid = true;

                parsePlans(data.response)

            }else{
                input.classList.add("coupon-error");
                input.classList.remove("coupon-success");
                input.dataset.valid = false;
                parsePlans()
            }
        }
    })
}
</script>
<div class="container-payment-element">
    <header>
        <h2 class="has-text-primary fw800 is-size-3 title-second-step">
            {formLang.title}
        </h2>
        <p class="has-text-dark f200 subtitle-second-step">
            {@html formLang.subtitle}
        </p>
        <div class="progress-bar second-step-progress-bar">
            <p class="steps-texts has-text-white progress-bar-text">
                Paso 3/3
            </p>
            <progress class="progress is-primary is-large" value="100" max="100">100%</progress>
        </div>
    </header>
    <main>
        <form action="" on:input|preventDefault="{ updateDataForm }" on:submit|preventDefault="{ submitPayment }" bind:this="{form}">
            <input type="hidden" name="stripeToken" value={stripeToken}>
            <input type="hidden" name="card_name_owner" value={stripeToken}>
            <div class="columns is-multiline">
                <section class="column is-6">
                    <div class="box box-cart">
                        <header>
                            <h2 class="fw600 has-text-white">
                                {lang.forms.paymentAnyPlan.title}
                            </h2>                            
                        </header>
                        <main>
                            <div class="is-flex is-justify-content-space-between is-align-items-center">
                                <h3 class="has-text-white is-flex is-align-items-center">
                                    <span class="icon icon-plans is-size-1">
                                        <span class="{plan.img_plan} icon plan-icons-container"><span class="path1"></span><span class="path2"></span></span>
                                    </span>
                                    <span class="fw400 is-size6-6 title-plan has-text-left">
                                        Plan: {plan.name_plan}
                                        <br>
                                        <span class="interval is-size-7">
                                            x 
                                            {#if plan.interval_selected === "year"}
                                                12 {lang.intervals.month[1]}
                                            {/if}
                                            {#if plan.interval_selected === "month"}
                                                1 {lang.intervals.month[0]}
                                            {/if}
                                            
                                        </span>
                                    </span>                                 
                                </h3>
                                    <p class="fw500 has-text-white is-size-6">
                                        {#if (plan.interval_current_price ?? null)  !== null}
                                            <span class="price-dontpay" style="text-decoration: line-through;">
                                                <span>
                                                    {Data.currency[0]}
                                                </span>
                                                <span>
                                                    {(plan.interval_selected === "year" ? (plan.interval_current_price*12) : plan.interval_current_price).toFixed(2)}
                                                </span>
                                                <span class="is-size-7">
                                                    {Data.currency[1]}
                                                </span>
                                            </span>
                                            <br>
                                        {/if}
                                        <span class="price-to-pay">
                                            <span>
                                                {Data.currency[0]}
                                            </span>
                                            <span>
                                                {(plan.interval_selected === "year" ? (plan.interval_price*12) : plan.interval_price).toFixed(2)}
                                            </span>
                                            <span class="is-size-6">
                                                {Data.currency[1]}
                                            </span>
                                        </span>
                                    </p>
                            </div>
                            <div>
                                <hr class="separtion-line">
                            </div>
                            <div>
                                {#if plan.interval_selected === "year"}
                                    <p class="has-text-white has-text-left is-flex is-justify-content-space-between">
                                        <span class="fw600">
                                            {lang.forms.paymentAnyPlan.price}
                                        </span>
                                        <span class="fw600">
                                            <span class="price-to-paid">
                                                <span>
                                                    {Data.currency[0]}
                                                </span>
                                                <span>
                                                    {plan.interval_price}
                                                </span>
                                                <span class="is-size-6">
                                                    {Data.currency[1]}
                                                </span>
                                            </span>
                                        </span>
                                    </p>
                                    <p class="has-text-white has-text-left is-flex is-justify-content-space-between">
                                        <span class="fw600">
                                            {lang.forms.paymentAnyPlan.months}
                                        </span>
                                        <span class="fw600">
                                            {#if plan.interval_selected === "year"}
                                                x 12
                                            {/if}
                                        </span>
                                    </p>
                                {/if}                                                              
                                {#if typeof plan.interval_discount !== "undefined"}
                                    <!-- subtotals -->
                                    <p class="has-text-white has-text-left is-flex is-justify-content-space-between">
                                        <span class="fw600">
                                            {lang.forms.paymentAnyPlan.subtotal}
                                        </span>
                                        <span class="fw600">
                                            <span class="price-interval">
                                                <span>
                                                    {Data.currency[0]}
                                                </span>
                                                <span>
                                                    {(plan.interval_selected === "year" ? (plan.price_on_year_yearly) : plan.interval_current_price).toFixed(2)}
                                                </span>
                                                <span class="is-size-6">
                                                    {Data.currency[1]}
                                                </span>
                                            </span>
                                        </span>
                                    </p>  
                                    <!-- discounts -->
                                    <p class="has-text-white has-text-left is-flex is-justify-content-space-between">
                                        <span class="fw600">
                                            {lang.forms.paymentAnyPlan.discounts}
                                        </span>
                                        <span class="fw600">
                                            <span class="price-discounts">
                                                - <span>
                                                    {Data.currency[0]}
                                                </span>
                                                <span>
                                                    {(plan.interval_selected === "year" ? (plan.interval_discount*12) : plan.interval_discount).toFixed(2)}
                                                </span>
                                                <span class="is-size-6">
                                                    {Data.currency[1]}
                                                </span>
                                            </span>
                                        </span>
                                    </p>
                                    <hr class="separtion-line">
                                {/if}
                                <p class="has-text-white has-text-left is-size-5 is-flex is-justify-content-space-between">
                                    <span class="fw600">
                                        {lang.forms.paymentAnyPlan.total}
                                    </span>
                                    <span class="fw600">
                                        <span class="price-to-paid">
                                            <span>
                                                {Data.currency[0]}
                                            </span>
                                            <span>
                                                {(plan.interval_selected === "year" ? (plan.interval_price*12) : plan.interval_price).toFixed(2)}
                                            </span>
                                            <span class="is-size-6">
                                                {Data.currency[1]}
                                            </span>
                                        </span>
                                    </span>
                                </p>
                            </div>
                        </main>
                    </div>
                </section>
                <section class="column is-6">
                    <div class="columns is-multiline" style="padding: 1.45rem 0">
                        {#if trial === false}
                            <div class="field field-payment-funnels column is-12 is-marginless is-paddingless-bottom">
                                <div class="control has-text-left">
                                    <label for="" class="fw600 label-text">{formLang.card_name_owner}</label>
                                    <input type="text" value="{$StoreForm?.dataForm["card_name_owner"] ?? ""}" name="card_name_owner" placeholder="{formLang.card_name_owner}" class="input formElements is-rounded is-hovered" minlength="4" required>
                                </div>
                            </div>
                            <div class="field column field-payment-funnels field-stripe-card is-12-touch is-marginless">
                                <div class="control has-text-left">
                                    <label for="" class="fw600 label-text">{formLang.card}</label>
                                    <StripeCard public_key={stripe_key} />                    
                                </div>
                            </div>
                            {#if hasDiscountFromTag === false}
                                <div class="field column is-4 field-payment-funnels field-coupon is-12-touch is-marginless">
                                    <div class="control has-text-left">
                                        <label for="" class="fw600 label-text">{formLang.coupon}</label>
                                        <input bind:this="{couponElement}" on:input={checkCoupon} type="text" value="{$StoreForm?.dataForm["coupon"] ?? ""}" name="coupon" placeholder="{formLang.coupon}" class="input formElements is-rounded is-hovered coupon" minlength="4">
                                    </div>
                                </div>
                            {/if}
                            <div class="field field-payment-funnels field-card-images column is-12 is-marginless">
                                <div class="columns is-multiline is-centered is-flex-mobile">
                                    <div class="field column is-6 secure-cards is-6-mobile is-marginless has-text-left">
                                        <div class="control cards">
                                            <span class="span-card-images1">
                                                <figure class="card-figure card-image-svg-1">
                                                    <svg width="100%" height="100%" version="1.1" viewBox="0 0 24.106 16.071" xmlns="http://www.w3.org/2000/svg">
                                                        <g fill="#415b76">
                                                            <path d="m21.6 0h-19.089a2.515 2.515 0 0 0-2.511 2.511v11.049a2.515 2.515 0 0 0 2.511 2.507h19.089a2.515 2.515 0 0 0 2.511-2.511v-11.045a2.515 2.515 0 0 0-2.511-2.511zm1.5 13.56a1.508 1.508 0 0 1-1.5 1.507h-19.089a1.508 1.508 0 0 1-1.511-1.507v-11.049a1.508 1.508 0 0 1 1.507-1.507h19.093a1.508 1.508 0 0 1 1.5 1.507z"/>
                                                            <path d="m23.6 3.013h-23.1a0.5 0.5 0 0 0-0.5 0.5v3.013a0.5 0.5 0 0 0 0.5 0.5h23.1a0.5 0.5 0 0 0 0.5-0.5v-3.013a0.5 0.5 0 0 0-0.5-0.5zm-0.5 3.013h-22.1v-2.009h22.1z"/>
                                                            <path d="m16.798 13.058a0.5 0.5 0 0 1-0.354-0.146l-1.048-1.049a0.5 0.5 0 0 1 0-0.707 0.5 0.5 0 0 1 0.707 0l0.695 0.695 2.441-2.442a0.5 0.5 0 0 1 0.707 0 0.5 0.5 0 0 1 0 0.707l-2.796 2.795a0.5 0.5 0 0 1-0.352 0.147z"/>
                                                            <path d="m3.009 11.474h3.213a0.4 0.4 0 1 0 0-0.8h-3.213a0.4 0.4 0 1 0 0 0.8z"/>
                                                            <path d="m7.829 11.474h3.213a0.4 0.4 0 0 0 0-0.8h-3.213a0.4 0.4 0 1 0 0 0.8z"/>
                                                            <path d="m3.009 13.081h5.221a0.4 0.4 0 1 0 0-0.8h-5.221a0.4 0.4 0 1 0 0 0.8z"/>
                                                            <path d="m11.041 12.278h-1.2a0.4 0.4 0 0 0 0 0.8h1.2a0.4 0.4 0 1 0 0-0.8z"/>
                                                        </g>
                                                        </svg>
                                                </figure>
                                            </span>
                                            <span class="span-card-images2">
                                                <img class="card-images" src="https://app.3pod.io/default/public/assets/_images_assets_/gallery/public/D2JUAU.png" alt="Credit/Debit Cards">
                                            </span>
                                        </div>
                                    </div>
                                    <div class="field column is-6 secure-cards is-6-mobile is-marginless has-text-left is-marginless">
                                        <div class="control cards field-card-images">
                                            <div class="is-flex">
                                                <span>
                                                    <figure class="card-figure card-image-svg-2">
                                                        <svg width="100%" height="100%" version="1.1" viewBox="0 0 18.219 21.184" xmlns="http://www.w3.org/2000/svg">
                                                            <g transform="translate(-33.21)">
                                                            <g transform="translate(33.21)" fill="#415b76">
                                                            <path d="m9.1094 2.7031a18.117 18.117 0 0 1-6.7051 3c-0.481 8.425 6.7001 12.786 6.7051 12.783s7.1871-4.3582 6.7051-12.783a18.981 18.981 0 0 1-6.7051-3zm1.9219 5.9922a0.5 0.5 0 0 1 0.35352 0.14648 0.5 0.5 0 0 1 0 0.70703l-2.7969 2.7949a0.5 0.5 0 0 1-0.35156 0.14648 0.5 0.5 0 0 1-0.35352-0.14453l-1.0488-1.0488a0.5 0.5 0 0 1 0-0.70703 0.5 0.5 0 0 1 0.70703 0l0.69531 0.69336 2.4414-2.4414a0.5 0.5 0 0 1 0.35352-0.14648z"/>
                                                            <path transform="translate(-33.21)" d="m51.4 4.35a0.563 0.563 0 0 0-0.452-0.52 23.726 23.726 0 0 1-8.292-3.718 0.563 0.563 0 0 0-0.677 0 22.5 22.5 0 0 1-8.287 3.718 0.563 0.563 0 0 0-0.452 0.52 18.268 18.268 0 0 0 5.222 13.743 21.416 21.416 0 0 0 2.473 2.242c0.34 0.261 0.615 0.453 0.785 0.568 0.318 0.215 0.41 0.277 0.585 0.28h0.021c0.173 0 0.278-0.07 0.553-0.255 0.149-0.1 0.391-0.267 0.692-0.495a21.113 21.113 0 0 0 2.222-1.948 20.072 20.072 0 0 0 3.94-5.548 17.516 17.516 0 0 0 1.667-8.587zm-4.933 11.694a19.732 19.732 0 0 1-4.147 3.906 19.728 19.728 0 0 1-4.148-3.907 16.68 16.68 0 0 1-3.826-11.2 24.008 24.008 0 0 0 7.977-3.587 24.641 24.641 0 0 0 7.971 3.587 16.68 16.68 0 0 1-3.827 11.201z"/>
                                                            </g>
                                                            </g>
                                                        </svg>
                                                    </figure>
                                                </span>
                                                <span class="is-size-7">
                                                    &nbsp;{formLang.securePayment}
                                                </span>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        {/if}
                        <div class="field field-payment-funnels column is-12 is-paddingless-top is-marginless">
                            <div class="control">
                                <div class="field">
                                    <input class="is-checkradio is-circle is-success is-small" id="agree-terms-3pod" value="true" type="checkbox" name="agree-terms" required>
                                    <label for="agree-terms-3pod" class="is-pulled-left">
                                        <p class="has-text-left is-size-7" bind:this="{terms}">
                                            {#each (lang?.forms?.guarantee ?? []) as item}
                                                {@html item}
                                            {/each}
                                        </p>
                                    </label>
                                </div>
                            </div>
                        </div>
                        
                        <div class="field field-payment-funnels column is-12 is-marginless">
                            <div class="navigation under--navigation-text">
                                <p class="is-underlined pointer under--navigation-back">
                                    <span on:click={changeStep("ChoosePlan")}>
                                        {lang.goBack}
                                    </span>
                                </p>
                            </div>
                            <div class="buttons is-grouped is-centered">
                                <button bind:this={buttonSubmit} type="submit" class="button submit-button-pay is-rounded is-success has-text-white">
                                    {formLang.button}
                                </button>
                            </div>
                        </div>
                    </div>                    
                </section>                
            </div>
        </form>
    </main>
</div>
<style>
.separtion-line{
    margin-top: 2rem;
    margin-bottom: 2rem;
}
.icon-plans{
    font-size:52px;
}
.title-plan{
    margin-left: 1.25rem;
}
.path2:before, .path1:before{
    color:#fff;
}
.box-cart{
    background: transparent linear-gradient(180deg, #344fff 0%, #09E2FF 100%) 0% 0% no-repeat padding-box;
    height: 100%;
    padding: 2.25rem 3.25rem;
}
.container-payment-element{
    width: 100%;
    padding: 0 10%;
}
.is-paddingless-bottom{
    padding-bottom: 0;
}
.is-paddingless-top{
    padding-top: 0;
}
@media only screen and (max-width: 1023px){
.container-payment-element{
    width: 100%;
    padding: 0 5%;
}
}
main{
    margin-top: 1.25rem;
    padding-top: 1.25rem;
}
.coupon:global(.coupon-success), .coupon:global(.coupon-success:focus){
    border: 1px solid var(--color-green);
}
.coupon:global(.coupon-error), .coupon:global(.coupon-error:focus){
    border: 1px solid var(--color-red);
}
.card-images{
    max-height: 20px;
}
progress[value]::-webkit-progress-value{
    background: transparent linear-gradient(90deg, #344FFF 0%, #09E2FF 100%) 0% 0% no-repeat padding-box;
}
.progress-bar{
    margin-top: 1.25rem !important;
    padding: 1rem 0;
    position: relative;
    width: 100%;
    max-width: 400px;
    margin: auto;
}
.steps-texts{
    position: absolute;
    width: 100%;
    text-align: center;
    top: 1rem;
    left: 0.45rem;
}
header{
    position: relative;
    margin-bottom: 1.25rem;
}
.secure-cards{
    padding-top: 0;
}
figure.card-figure svg{
    max-height:18px;
}
.control.cards{
    display: flex;
}
@media only screen and (max-width:768px){
    .field-payment-funnels{
        padding: 0.25rem 0.75rem;
    }
}
</style>