<script>
import InternationalNumberInput from './../../components/Elements/InternationalNumberInput.svelte';
import { StoreForm } from './StoreForm.js';
import { AppForms } from "../../libraries/AppForms";
import { turpial } from "../../libraries/turpial";
import { InjectScript } from '../../libraries/InjectScript';


export let changeStep;
export let Data;
let has_chooser_plan_module = Data.has_chooser_plan_module ?? false;
let userFromCampaign = Data.userFromCampaign;
let is_online = Data.is_online ?? true; 
export let errors;
let getMembership = ()=>{
    return tripod_register.membership;
} 

let host = "https://app.3pod.io/";
if(is_online === false){
    host = Data?.host_offline ?? "http://localhost/app.3pod.io/"
}


let typeOfSignUp = Data.type ?? "default";
let lang = Data.lang;
let buttonSubmit = null;
let formLang = lang.forms.basic;
let form = null;


tripod_register.submit = function(){
    catchData(form, function(data){
        let hcaptcha = document.querySelector("iframe[data-hcaptcha-response]");
        data.captcha = hcaptcha.getAttribute("data-hcaptcha-response")
        return data;
    });
}
function checkForm(data){
    let is_all_valid = true;
    data = data || {};

    // checks validation inputs.
    let phone = form.querySelector(".int-phone");
    if(phone.dataset.valid === "false"){ is_all_valid = false }


    // check empty inputs
    let checks = ["first-name", "last-name", "email", "phone"];
    checks.map(( element )=>{
        if(typeof data[element] !== "string"){
            is_all_valid = false;
        }
        if(data[element] === ""){
            console.log(data[element])
            is_all_valid = false;
        }
    });

    
    
    return is_all_valid;
}

if(typeOfSignUp === "trial" || typeOfSignUp === "basic"){
    InjectScript("https://js.hcaptcha.com/1/api.js", {
        attrs: {
            async: "",
            defer: "",
        },
        onload: ()=>{
            hcaptcha.render(buttonSubmit, {
                sitekey: 'bf7e6dce-eb06-4ad7-8aa1-cddcbd4f365d',
                'data-callback': 'captchaSubmit',
            });
        }
    })
}



function catchData(target, before){
    before = before||function(data){ return data; }
    errors.hide();
    let form = target;
    
    let data = AppForms.retrieve({
        targets: ".formElements",
        form: form,
    });
    if(checkForm(data) === false){ 
        AppForms.statusButton(buttonSubmit, "enabled");
        errors.showError(lang.errors.uncompleted);
        return;
    }
    data = before(data);

    data.userFromCampaign = userFromCampaign;

    data.password_again = data.password;
    let dataForm = data;
    let encodeEmail = btoa(data.email).split("").reverse().join("")
    AppForms.statusButton(buttonSubmit, "disabled");
    

    let getPlansUrl = host+"api/getPlans";
    console.log(Data?.userFromCampaign)
    if(typeof Data?.userFromCampaign === "string"){
        getPlansUrl = `${host}api/getPlans?tag=${btoa(Data?.userFromCampaign)}`
    }

    let method = "GET"
    let connections = [
        getPlansUrl,
        host+"api/isUserRegistered/"+encodeEmail
    ];
    if(typeOfSignUp === "trial" || typeOfSignUp === "basic"){
        method = "POST";
        connections = [
            host+"public/api/v1/registerUser/"
        ];
    }

    turpial.fetch({
        files: connections,
        method: method,
        type: "text",
        data: JSON.stringify(data),
        ready: (data)=>{
            // on default
            switch (typeOfSignUp) {
                case "default":
                    let plans = JSON.parse(data[0]).response[getMembership()];
                    let isEmailRegistered = JSON.parse(data[1]);
                    AppForms.statusButton(buttonSubmit, "enabled");
                    
                    if(isEmailRegistered.response === true){
                        errors.showError(lang.errors.email_registered);
                        return;
                    }
                    
                    let plans_selected = plans.filter(( plan )=>{
                        // buscar entre los seleccionados en tripod_register
                        if(typeof tripod_register.plans_selected.find(( selected )=>{
                            if(selected === plan.__id__){
                                return selected;
                            }
                        }) !== "undefined"){
                            // si se encuentran coicidencias devolver el plan
                            return plan;
                        }
                    })

                    tripod_register.plans = plans;
                    tripod_register.dataForm = dataForm;            

                    tripod_register.plansFetched = plans_selected;

                    StoreForm.update("plansFetched", plans_selected);
                    StoreForm.update("plans", plans);
                    StoreForm.update("dataForm", dataForm, $StoreForm?.dataForm);
                    console.log(has_chooser_plan_module)
                    if(has_chooser_plan_module === true){
                        changeStep("ChoosePlan");
                        return;
                    }
                    changeStep("PaymentFunnels");
                break;
                case "trial":
                    data = JSON.parse(data[0]);
                    if(data.status === "error"){
                        AppForms.statusButton(buttonSubmit, "enabled");
                        errors.showError(data.message);
                        return;
                    }
                    if(data.status === "success"){
                        if(data?.response?.url_sign_in){
                            let url = data?.response?.url_sign_in;
                            window.location.href = url;
                        }
                        return;
                    }
                case "basic":
                    data = JSON.parse(data[0]);
                    if(data.status === "error"){
                        AppForms.statusButton(buttonSubmit, "enabled");
                        errors.showError(data.message);
                        return;
                    }
                    if(data.status === "success"){
                        if(data?.response?.url_sign_in){
                            let url = data?.response?.url_sign_in;
                            window.location.href = url;
                        }
                        return;
                    }
                break;
                default:
                    break;
            }
            
        },
        onerror: (data)=>{
            console.log.error(data)
            AppForms.statusButton(buttonSubmit, "enabled");
        }
    });
}
let pattern = ".{6,}"
</script>
<style>
form{
    position: relative;
    padding: 1.75rem 2.25rem;
}
@media only screen and (max-width:768px){
    form{
        padding: 1.75rem 0rem;
    }
}
form .field{
    padding: 0.5rem 0.45rem;
}
form .field .control{
    text-align: left;
}
</style>
<header>
    <h2 class="has-text-primary fw800 is-size-3 title-first-step">
        {lang.title}
    </h2>
    <p class="has-text-dark f200 subtitle-first-step">
        {@html lang.subtitle}
    </p> 
    {#if has_chooser_plan_module}
        <div class="progress-bar second-step-progress-bar" style="max-width: 320px;">
            <p class="steps-texts has-text-primary progress-bar-text">
                Paso 1/3
            </p>
            <progress class="progress is-primary is-large" value="33" max="100">100%</progress>
        </div>
    {/if}               
</header>
<main>
    <form class="columns is-multiline" action="" onsubmit="event.preventDefault()" method="POST" bind:this="{form}" on:submit|preventDefault="{(e)=>{ catchData(e.target) }}">
        <div class="field column is-6">
            <div class="control has-text-left">
                <label for="" class="fw600 label-text">{formLang.name} <span class="has-text-danger">*</span></label>
                <input type="text" value="{$StoreForm?.dataForm["first-name"] ?? ""}" name="first-name" placeholder="{formLang.name}" class="input formElements is-rounded is-hovered" minlength="2" required>
            </div>
        </div>
        <div class="field column is-6">
            <div class="control has-text-left">
                <label for="" class="fw600 label-text">{formLang.lastname} <span class="has-text-danger">*</span></label>
                <input type="text" name="last-name" value="{$StoreForm?.dataForm["last-name"] ?? ""}" placeholder="{formLang.lastname}" class="input formElements is-rounded is-hovered" minlength="2" required>
            </div>
        </div>
        <div class="field column">
            <div class="control has-text-left">
                <label for="" class="fw600 label-text">{formLang.email} <span class="has-text-danger">*</span></label>
                <input type="email" name="email" value="{$StoreForm?.dataForm["email"] ?? ""}" placeholder="E-mail" class="input formElements is-rounded is-hovered" minlength="4" required>
            </div>
        </div>
        <div class="field field-payment-funnels column intl-phone-container">
            <div class="control has-text-left">
                <label for="" class="fw600 label-text">{lang.forms.planSelection.phone} <span class="has-text-danger">*</span></label>
                <InternationalNumberInput 
                    selected_code={$StoreForm.dataForm?.code||"CO"}
                    selected_dial={$StoreForm.dataForm?.dial||"+57"}
                    selected_country={$StoreForm.dataForm?.country||"Colombia"}
                    selected_value={$StoreForm.dataForm?.phone||""}
                    ClassInputs="formElements int-phone" />
            </div>
        </div>
        <div class="field column is-12">
            <div class="control has-text-left">
                <label for="" class="fw600 label-text">{lang.forms.basic.password} <span class="has-text-danger">*</span></label>
                <input pattern={pattern} title="{formLang.errors.password_error}" type="password" value="{$StoreForm?.dataForm["password"] ?? ""}" name="password" placeholder="{lang.forms.basic.password}" class="input formElements is-rounded is-hovered" required>
            </div>
        </div>
        <div class="field column is-12">
            <p class="is-size-7 fw400 has-text-left require-fields-text">
                {@html formLang.adviseRequired}
            </p>
        </div>
        <div class="field column is-12">
            <div class="buttons is-grouped is-centered submit-buttons-continue">
                {#if typeOfSignUp === "trial" || typeOfSignUp === "basic"}
                        <button bind:this={buttonSubmit} data data-callback="captchaSubmit" type="submit" data-size="invisible" class="button is-rounded is-success has-text-white">
                            {formLang.button}
                        </button>
                    {:else}
                        <button bind:this={buttonSubmit} type="submit" class="button is-rounded is-success has-text-white">
                            {formLang.button}
                        </button>
                {/if}
                
            </div>
        </div>
        {#if (formLang?.sign_in?.active ?? true) === true && typeOfSignUp === "basic"}
            <div class="field column is-12">
                <p class="require-fields">
                    {formLang.sign_in.already_has} <span class="fw600 has-text-dark has-text-left">
                        <a href="{host}user/login" class="has-text-dark is-underlined">
                            {formLang.sign_in.in}
                        </a>
                    </span>
                </p>
            </div>
        {/if}   
        {#if (formLang?.sign_in?.active ?? false) === true && typeOfSignUp === "default"}
            <div class="field column is-12">
                <p class="require-fields">
                    {formLang.sign_in.already_has} <span class="fw600 has-text-dark has-text-left">
                        <a href="#/" on:click="{()=>{ changeStep("SignInForm") }}" class="has-text-dark is-underlined">
                            {formLang.sign_in.in}
                        </a>
                    </span>
                </p>
            </div>
        {/if}
        {#if typeOfSignUp === "trial" || typeOfSignUp === "basic"}
            <div class="field column is-12">
                <p class="is-size-7 fw200 has-text-centered is-sr-only">
                    This site is protected by hCaptcha and its
                    <a rel="noopener,noreferrer" target="_blank" href="https://hcaptcha.com/privacy">Privacy Policy</a> and
                    <a rel="noopener,noreferrer" target="_blank" href="https://hcaptcha.com/terms">Terms of Service</a> apply.
                </p>
            </div>
        {/if}
    </form>

</main>