<script>
import { StoreForm } from './../register/steps/StoreForm.js';
import CurrencyFormatter from './../components/Elements/CurrencyFormatter.svelte';
import { PlansStore } from './Plans.store.js';
import { parseHtmlString } from "../libraries/parseHtmlString";
export let tripod_plans;
export let interval;
export let affiliate = {};
export let onSelectPlan = function(){}; // for internal modules
let metadata = tripod_plans;
let showProducts = tripod_plans?.showProducts ?? true;
let currentPlan = tripod_plans?.currentPlan ?? false;
let lang = metadata.lang;


// this will work only in register widget.
let subscriptions = [];
if(($StoreForm?.dataForm?.subscriptions)){
    subscriptions = $StoreForm?.dataForm?.subscriptions ?? [];
}



function selectPlan(event, plan){
    let button = event.target;

    // will be executed outside any module
    tripod_plans.onSelect(plan, interval, button);

    // will be executed in internal modules
    onSelectPlan({
        plan: plan, interval: interval, button: button
    });
}

let parsedPlans = [];

$: {
    if($PlansStore){
        parsedPlans = $PlansStore.plans_views.filter(( plan )=>{
            let discountPlans = affiliate?.referralDiscounts?.plans ?? [];
            let interval_price;
            let percentOff = 0;
            plan.has_discount = "";
            plan.is_selected = false;
            Object.values(subscriptions).map(( subscription )=>{
                if(plan.__id__ === subscription.id_plan && subscription.registered_at_stripe === true){
                    plan.is_selected = true;
                }
            })

            if(interval !== "month"){
                if(typeof plan[`interval_${interval}_price`] === "undefined" || plan[`interval_${interval}_price`] === ""){
                    interval_price = plan.price_plan;
                    percentOff = 0;
                }else{
                    interval_price = plan[`interval_${interval}_price`];
                    percentOff = (plan.price_plan*100)/plan[`interval_${interval}_price`]-100;
                    percentOff = isNaN(percentOff) === false ? percentOff : 0;
                    percentOff = Number.parseInt(percentOff);
                    plan.has_discount = "has_discount";
                }
            }else{
                interval_price = plan.price_plan;
                percentOff = 0;
            }
            function onDicountsPlan(discountPlan){
                if(discountPlan.plan === plan.__id__){
                    plan.interval_current_price = interval_price;            
                    interval_price = interval_price - ((discountPlan.discountPercent/100)*interval_price);
                    plan.interval_discount = plan.interval_current_price - interval_price;
                    plan.hasReferralDiscounts = discountPlan.discountPercent;
                    plan.has_discount = "has_discount";
                }
            }
            plan.interval_selected = interval;
            // in case of discounts by referrals
            discountPlans.map(( dp )=>{
                onDicountsPlan(dp);
            })


            // in case of discounts by tag
            if((plan.discountsPlan ?? null) !== null){
                onDicountsPlan(plan.discountsPlan);
            }

            interval_price = Number.parseFloat(Number.parseFloat(interval_price).toFixed(2))
            plan.interval_price = interval_price;
            plan.percentOff = percentOff;

            let currency = plan.currency ?? null !== null ? plan.currency : tripod_plans.currency;
            let is_recommended = ((plan.is_recommended ?? "Default") === "Recomendado") ? "recommended" : plan.is_recommended||"";
            let yearly = typeof plan.interval_year_price === "undefined" ? null : plan.interval_year_price;
            plan.currency = currency;
            plan.is_recommended = is_recommended;
            plan.yearly = is_recommended;
            plan.is_available = plan.is_available || "public";

            if(plan.is_available === "public"){
                return plan;
            }
        })
    }
}




</script>
{#if $PlansStore.plans_views.length <= 0}
    Cargando planes...
{/if}
{#each parsedPlans as plan}
    {#if plan.type === "PLAN"}
        <div class="column column-card-plan">
            <section class="{plan.is_recommended} is-plan">
                <div class="container-discount">
                    {#if typeof plan?.hasReferralDiscounts === "undefined"}
                        {#if Number.isInteger(plan.percentOff)}
                            {#if plan.percentOff > 0}
                                <p class="has-text-left">
                                    <span class="tag is-small percent-off-tag tag-percent fw400 is-rounded {plan.is_recommended} tag-percent-off">
                                        Ahorra {plan.percentOff}%
                                    </span>
                                </p>
                            {/if}
                        {/if}
                        {:else}
                            <p class="has-text-left">
                                <span class="tag is-small percent-off-tag tag-percent fw400 is-rounded {plan.is_recommended} tag-percent-off">
                                    Ahorra {plan.hasReferralDiscounts}%
                                </span>
                            </p>
                    {/if}
                </div>
                <header class="is-flex">
                    {#if plan.is_recommended === "Recomendado" || plan.is_recommended === "recommended"}
                        <h4 class="defaultItem {plan.has_discount}  has-text-centered">
                            {metadata.lang.defaultItem}
                        </h4>
                    {/if}
                    <div>                    
                        <h2 class="is-size-4 has-text-weight-bold is-flex Title-Plan-{plan.is_recommended}">
                            <span>
                                <span class="{plan.img_plan} plan-icons-container"><span class="path1"></span><span class="path2"></span></span>
                            </span>
                            <span class="name_plan-span">
                                {plan.name_plan}
                            </span>
                        </h2>
                        {#if typeof plan.hasReferralDiscounts !== "undefined"}
                            <p class="has-text-centered is-size-4 has-text-weight-semibold price-element-container dontpay-price">
                                <span class="price-element">
                                    <span class="is-size-7 has-text-weight-light currency-symbol">
                                        {plan.currency[0]}
                                    </span>
                                    <span style="text-decoration: line-through;">
                                        <CurrencyFormatter parseCents={true} number={plan.interval_current_price} />
                                    </span>
                                </span>
                                <span class="is-size-7 has-text-weight-light currency-code">
                                    {plan.currency[1]}
                                </span><span class="is-size-7 has-text-weight-semibold"> / 
                                    {metadata.lang.eachMonth}
                                </span>
                            </p>
                        {/if}
                        <p class="has-text-centered is-size-3 has-text-weight-semibold price-element-container main-price {plan.is_recommended}">
                            <span class="price-element">
                                <span class="is-size-6 has-text-weight-light currency-symbol">
                                    {plan.currency[0]}
                                </span>
                                <CurrencyFormatter parseCents={true} number={plan.interval_price} />
                            </span>
                            <span class="is-size-7 has-text-weight-light currency-code">
                                {plan.currency[1]}
                            </span><span class="is-size-7 has-text-weight-semibold"> / 
                                {metadata.lang.eachMonth}
                            </span>
                        </p>
                        {#if plan.yearly !== null && interval === "year"}
                            {#if plan[`interval_${interval}_price`] ?? "" !== ""}
                                <p class="has-text-centered is-size-7">
                                    {plan.currency[0]}<span>
                                        {#if interval === "year"}
                                            <CurrencyFormatter parseCents={true} number={plan.interval_price*12} />
                                        {/if}
                                    </span> 
                                    {plan.currency[1]} / 
                                    <span class="is-lowercase">
                                        {metadata.lang.year}
                                    </span>
                                </p>
                            {/if}
                            
                        {/if}                    
                    </div>
                </header>
                <main>
                    <ul class="container-items">
                        {#each Object.values(JSON.parse(plan.items)) as item}
                            <li class="item-list">
                                <span class="icon">
                                    <i class="fas fa-check"></i>
                                </span>
                                <span class="content-item">
                                    {@html parseHtmlString(item)}
                                </span>
                            </li>
                        {/each}
                    </ul>
                </main>
                <footer>
                    {#if currentPlan.__id__ !== plan.__id__ && plan.is_selected === false}
                        <div class="buttons is-grouped is-centered button-container-select">
                            <button 
                                on:click="{(e)=>{ selectPlan(e, plan) }}"
                                data-plan-id="{plan.__id__}" 
                                data-price-on-year-monthly="{plan.price_on_year_monthly}"
                                data-position-plan="{plan.position_plan}"
                                data-type="{plan.type}"
                                data-membership-type="{plan.membership_type}"
                                data-interval="{interval}"
                                type="button" class="button is-success is-rounded fw400 has-text-white button-success-3pod">
                                <span class="is-capitalized">
                                    {lang.setPlan}
                                </span>
                            </button>
                        </div>
                        {:else}
                            <h4 class="text-on-current has-text-centered">
                                {lang.currentPlan}
                            </h4>
                    {/if}
                </footer>
            </section>
        </div>
    {/if}    
{/each}
{#if showProducts === true}
    <div class="column is-12">
        <hr>
    </div>
    {#each parsedPlans as plan}
        {#if plan.type === "PRODUCT"}
            <div class="column is-12">
                <div class="box">
                    <section class="is-product is-flex">
                        <header style="min-width: 260px;">
                            <div class="container-discount">
                                {#if typeof plan?.hasReferralDiscounts === "undefined"}
                                    {#if Number.isInteger(plan.percentOff)}
                                        {#if plan.percentOff > 0}
                                            <p class="has-text-left">
                                                <span class="tag is-small percent-off-tag tag-percent fw400 is-rounded {plan.is_recommended} tag-percent-off">
                                                    Ahorra {plan.percentOff}%
                                                </span>
                                            </p>
                                        {/if}
                                    {/if}
                                    {:else}
                                        <p class="has-text-left">
                                            <span class="tag is-small percent-off-tag tag-percent fw400 is-rounded {plan.is_recommended} tag-percent-off">
                                                Ahorra {plan.hasReferralDiscounts}%
                                            </span>
                                        </p>
                                {/if}
                            </div>
                            <h2 class="is-size-4 has-text-weight-bold is-flex Title-Plan-{plan.is_recommended}">
                                <span>
                                    <span class="{plan.img_plan} plan-icons-container"><span class="path1"></span><span class="path2"></span></span>
                                </span>
                                <span class="name_plan-span">
                                    {plan.name_plan}
                                </span>
                            </h2>
                            {#if typeof plan.hasReferralDiscounts !== "undefined"}
                            <p class="has-text-centered is-size-4 has-text-weight-semibold price-element-container-product dontpay-price">
                                    <span class="price-element">
                                    <span class="is-size-7 has-text-weight-light currency-symbol">
                                        {plan.currency[0]}
                                    </span>
                                    <span style="text-decoration: line-through;">
                                        <CurrencyFormatter parseCents={true} number={plan.interval_current_price} />
                                    </span>
                                </span>
                                <span class="is-size-7 has-text-weight-light currency-code">
                                    {plan.currency[1]}
                                </span><span class="is-size-7 has-text-weight-semibold"> / 
                                    {metadata.lang.eachMonth}
                                </span>
                            </p>
                            {/if}
                            <p class="is-block is-size-3 has-text-weight-semibold price-element-container-product main-price {plan.is_recommended}">
                                <span class="price-element">
                                    <span class="is-size-6 has-text-weight-light currency-symbol">
                                        {plan.currency[0]}
                                    </span>
                                    <CurrencyFormatter parseCents={true} number={plan.interval_price} />
                                </span>
                                <span class="is-size-7 has-text-weight-light currency-code">
                                    {plan.currency[1]}
                                </span><span class="is-size-7 has-text-weight-semibold"> / 
                                    {metadata.lang.eachMonth}
                                </span>
                            </p>
                        </header>
                        <main>
                            <ul class="container-items-product">
                                {#each Object.values(JSON.parse(plan.items)) as item}
                                    <li class="item-list item-list-product">
                                        <span class="icon">
                                            <i class="fas fa-check"></i>
                                        </span>
                                        <span class="content-item">
                                            {@html parseHtmlString(item)}
                                        </span>
                                    </li>
                                {/each}
                            </ul>                            
                        </main>                        
                    </section>
                    <div>
                        <div class="buttons is-centered is-grouped button-container-select-product">
                            <button 
                                on:click="{(e)=>{ selectPlan(e, plan) }}"
                                data-plan-id="{plan.__id__}" 
                                data-price-on-year-monthly="{plan.price_on_year_monthly}"
                                data-position-plan="{plan.position_plan}"
                                data-type="{plan.type}"
                                data-membership-type="{plan.membership_type}"
                                data-interval="{interval}"
                                type="button" class="button is-success is-rounded fw400 has-text-white button-success-3pod">
                                <span class="is-capitalized">
                                    {lang.setProduct}
                                </span>
                            </button>
                        </div>
                    </div>
                </div>
            </div>
        {/if}
    {/each}
{/if}

<style>

:global(.section) .Title-Plan-Default, :global(.section) .Title-Plan-Default .name_plan-span{
    color: var(--color-dark-grey);
}
:global(.section) .Title-Plan-recommended, :global(.section) .Title-Plan-recommended .name_plan-span{
    color: #fff;
}
header{
    justify-content: center;
    position: relative;
}
section.is-plan{
    color: #415B76;
    padding: 2.5rem 2rem;
    padding-top: 4rem;
    overflow: clip;
    position: relative;
    height: 100%;
    max-width: 425px;
    padding-bottom: 2rem;
    margin: auto;
}
section:global(.recommended){
    background: transparent linear-gradient(180deg, #344fff 0%, #09E2FF 100%) 0% 0% no-repeat padding-box;
    border-radius: 20px;
    transform: scale(1.06);
}
section:global(.recommended) header{
    color: #fff;
}
.main-price, .dontpay-price{
    position: relative;
    margin-bottom: 0;
}
.currency-symbol{
    position: relative;
    bottom: 12px;
}
.item-list{
    font-size: 0.85rem;
    padding: 0.25rem;
}
section:global(.recommended .item-list){
    color: #fff;
}
.item-list .icon{
    width: 9px;
    height: 9px;
    padding: 9px;
    font-size: 9px;
    border-radius: 40px;
    background: var(--color-primary);
    color: #fff;
}
section:global(.recommended .item-list .icon){
    background: var(--color-yellow);
    color: var(--color-dark-grey);
}
.text-on-current{
    color: #415B76;
}
section:global(.recommended .text-on-current){
    color: #fff;
}

.container-items{
    padding: 1.25rem 1rem;
}
.container-items-product{
    padding: 0.25rem 2rem;
}
.content-item{
    margin-left: 12px;
}
.item-list-product {
    display: inline-flex;
    width: 320px;
}
.defaultItem{
    position: absolute;
    transform: rotate(45deg);
    padding: 0 4rem !important;
    background: var(--color-yellow);
    color: var(--color-dark-grey);
    font-size: 14px !important;
    font-weight: 600 !important;
}
.defaultItem:global(.has_discount){
    top: -70px;
    right: -89px;
}
.defaultItem:not(.has_discount){
    top: -40px;
    right: -89px;
}
footer{
    padding: 0 !important;
    padding-top: 2rem !important;    
}
.button-container-select{
    left: 0;
    margin: auto;
    position: absolute;
    width: 100%;
    bottom: 1rem;
}
.button-container-select button{
    padding: 24px 22px;
}
.button-container-select-product{
    margin: 1.25rem auto;
}
.plan-icons-container{
    font-size: 3rem;
    margin-right: 1.25rem;
}
.plan-icons-container .path1:before, .plan-icons-container .path2:before{
    color: var(--color-primary);
}
.recommended .plan-icons-container .path1:before, .recommended .plan-icons-container .path2:before{
    color: #fff
}
.recommended .content-item :global(strong){
    color: #fff
}
.content-item :global(strong){
    color: var(--color-primary);
}
.column-card-plan{
    min-width: 340px;
}
.special-discount-from-referral{
    background: var(--color-purple) !important;
    color:#fff !important;
}
.main-price.recommended .price-element{
    color: var(--color-yellow);
    
}
.price-element, .price-element-container{
    display: flex;
    align-items: baseline;
}
.price-element-container{
    justify-content: center;
}
.price-element-container-product{
    margin-left: 4.25rem;
    color: var(--color-primary);
}
.tag-percent-off{
    color: var(--color-dark-grey);
    background-color: rgb(252 253 255 / 25%);
    color:#fff;
    font-size: 11px;
}
.percent-off-tag:not(.recommended){
    background: #f3f5ff;
    color: var(--color-black-bis);
}
.tag-percent{
    padding: 10px 12px;
}
.container-discount p{
    margin-bottom: 1rem;
    margin-left: 0.75rem;
}
</style>